import React, {useEffect, useState} from 'react';
import {Container, IconButton, ImageList, ImageListItem, ImageListItemBar, Typography} from "@mui/material";
import {getMenu} from "../data/MenuData";

const Menu = () => {
    const [loading, setLoading] = useState(false);
    const [menu, setMenu] = useState([]);
    useEffect(() => {
        const loadMenu = async () => {
            setLoading(true);
            const menu = await getMenu();
            console.log(menu);
            setMenu(menu);
            setLoading(false);
        }
        loadMenu();
    }, []);
    return (
        <Container>
            <Typography variant="h1"> Menu</Typography>
            {loading ? (
                    <h4>Loading...</h4>) :
                (
                    <ImageList
                        variant="quilted"
                        cols={4}
                        gap={8}
                        rowHeight={200}
                    >
                        {Object.keys(menu).map(item => {
                            const currentItem = menu[item];
                            return   <ImageListItem key={item}>
                                <img
                                    src={`${currentItem.image}?w=248&fit=crop&auto=format`}
                                    alt={item}
                                    loading="lazy"
                                />
                                <ImageListItemBar
                                    title={item}
                                    subtitle={`${currentItem.prices[0]} - ${currentItem.prices[currentItem.prices.length-1]}`}
                                    actionIcon={
                                        <IconButton
                                            sx={{ color: 'rgba(255, 255, 255, 0.54)' }}
                                            aria-label={"hola mi pez"}
                                        >
                                        </IconButton>
                                    }
                                />
                        </ImageListItem>
                    })}
                    </ImageList>
                )
            }
        </Container>
    );
}

export default Menu;